@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,400;0,600;1,900&display=swap');
* {
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
}
.nav-outline {
  background-color: #161d26f5;
  padding: 10px;
  display: flex;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
  box-shadow: 0px 0px 28px -15px rgb(0 0 0 / 75%);
  position: sticky;
  top: 0;
  z-index: 10;
  transition: all 0.2s ease-in-out;
  animation: nav-show 0.2s linear;
  opacity: 0;
  animation-fill-mode: forwards;
  color: white;
  transition: all 0.2s ease-in-out;
}
@keyframes nav-show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.nav-not-active {
  background-color: transparent;
  padding: 10px;
  display: flex;
  align-items: center;
  padding-left: 5%;
  padding-right: 5%;
  position: fixed;
  top: 0;
  color: white;
  z-index: 10;
  transition: all 0.2s ease-in-out;
}
.nav-logo {
  width: 80px;
}
.nav-menu h4 {
  cursor: pointer;
  font-weight: 400;
  font-size: 14px;
  margin-left: 30px;
  font-family: 'Source Sans Pro', sans-serif;
  transition: all 0.2s ease-in-out;
}
.nav-menu h4:hover {
  font-weight: 600;
  font-family: 'Source Sans Pro', sans-serif;
}
.nav-menu {
  display: flex;
  align-items: center;
  margin-left: 10px;
}
.jumbotron-outline {
  /* height: 800px; */
  height: 100vh;
}
.jumbotron-gif {
  height: 100%;
  width: 100%;
  background-image: linear-gradient(rgba(34, 47, 62,0.6), rgba(53, 59, 72,0.6)), url('https://blog.hyland.com/wp-content/uploads/F1-Alfa-Romeo-3loops.gif');
  background-attachment: fixed;
}

.jumbotron-information {
  position: relative;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: fit-content;
  color: white;
}
.jumbotron-information h2 {
  font-size: 110px;
  font-weight: 900;
  font-family: 'Source Sans Pro', sans-serif;
}
.jumbotron-information h5 {
  width: 500px;
  font-weight: 500;
  color: rgb(207, 207, 207);
}
.btn {
  background: linear-gradient(to right, transparent, #cc1200 ) ;
  padding: 15px 25px;
  border: 0;
  color: white;
  font-family: 'Source Sans Pro', sans-serif;
  margin-top: 10px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  border-top-right-radius: 10px;
}
.btn:hover {
  transform: scale(1.2);
}
.banner {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.banner-image {
  width: 200px;
  padding-left: 20px;
  animation: slide-in-out-banner 10s linear alternate;
}



@keyframes slide-in-out-banner {
  0% {
    transform: translateX(-100%);overflow: hidden;
  }
  50% {
    transform: translateX(100%);overflow: hidden;
  }
  100% {
    transform: translateX(100%);overflow: hidden;
  }
}
.world-cup-section {
  display: flex;
  /* margin: 8px 0px; */
  /* flex-wrap: wrap; */
  z-index: 1;
  justify-content: flex-start;
  position: relative;
  overflow-x: scroll;
  overflow-y: hidden;
}
.world-cup-section::-webkit-scrollbar {
  display: none;
}
.thumbnail-image {
  width: 100%;
  height: 300px;
  border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}
.thumbnail-outline {
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  background-color: #404040;
  margin: 0px 10px;
  width: 409px;
  /* border-radius: 10px; */
  height: 500px;
}
.thumbnail-outline:hover {
  transform: scale(1.01);
  -webkit-box-shadow: 0px 0px 28px -15px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 0px 28px -15px rgba(0,0,0,0.75);
  box-shadow: 0px 0px 28px -15px rgba(0,0,0,0.75);
}
.thumbnail-outline h5 {
  width: 400px;
  color: gray;
  font-weight: 400; 
  padding: 4px 10px;
  height: 13%;
}
.thumbnail-outline h3 {
  margin: 6px 0px;
  font-weight: 900;
  font-family: 'Source Sans Pro', sans-serif;
  color: #fff;
  font-size: 28px; 
  padding: 0px 10px;
}
.new-session {
  display: flex;
  align-items: center;
  justify-content: space-between;
  
  width: 100%;
  z-index: 1;
  position: relative;
}
.new-session-image {
  width: 50%;
  /* border-top-left-radius: 10px; */
  /* border-bottom-left-radius: 10px; */
}
.new-session-image-new-era {
  width: 50%;
  /* border-top-right-radius: 10px; */
  /* border-bottom-right-radius: 10px; */
}
.new-session h2 {
  font-size: 50px;
  color: #404040;
  width: 80%;
  text-transform: uppercase;
}
.new-session-information {
  padding: 0px 50px;
}
.new-session h4 {
  width: 500px;
  font-weight: 400;
  color: gray;
}
.fade-out {
  background: linear-gradient(to bottom, #212029, white);
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 0;
}
.footer-outline {
  width: 100%;
  background-color: #161d26;
  height: 350px;
  z-index: 11;
  position: relative;
}
.footer-img {
  width: 300px;
  border-radius: 50%;
  margin: 1.2% 0px;
}
.fade-out-top {
  background: linear-gradient(to top, #212029, white);
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 0;
}
.footer-header-grid {
  padding: 20px 25%;
  display: flex;

  justify-content: space-between;
}
.footer-header-grid h3 {
  color: white;
  font-size: 18px;
  font-weight: 400;
}
.footer-grid {
  display: flex;
  align-items: center;
  gap: 40px;
}
.footer-grid h4 {
  color: #a3a3a3;
  font-weight: 500;
  cursor: pointer;
  font-size: 12px;
}
.footer-grid h4:hover {
  color:#cc1200;
  font-weight: 500;
}
.footer-icons h4 {
  font-size: 18px;
}
.footer-logo {
  width: 60px;
  height: 90px;
}
.updates-in-game h1 {
  font-size: 50px;
  color: #ffffff;
  text-transform: uppercase;
  padding: 30px 0px;
  text-align: center;
  position: relative;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #ed1500;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b30000; 
}
.get-game-image-new-era {
  width: 100%;
}